export function inDictionary (word) {
  return wordList.includes(word);
}

export function getTargetWord (seed) {
  // const t0 = performance.now();
  const rng = mulberry32(seed);
  const numVowels = weightedPick([ 1, 2, 3 ], rng(), sylFreq);
  let word = null;
  let cycles = 0;
  while (!word) {
    const letters = [];
    let v0 = [ ...vowels ];
    let c0 = [ ...consonants ];
    for (let i = 0; i < 5; i++) {
      let x;
      if (i < numVowels) {
        x = weightedPick(v0, rng());
        v0 = v0.filter(d => d !== x);
      }
      else {
        x = weightedPick(c0, rng(), letterFreq);
        c0 = c0.filter(d => d !== x);
      }
      letters.push(x);
    }
    const list = permutations(letters.join('')).filter(inDictionary);
    if (list.length) {
      word = list[Math.floor(rng() * list.length)];
    }
    cycles++;
  }
  // const t1 = performance.now();
  // return { word, cycles, time: t1 - t0 };
  return word;
}
